<template>
  <div class="film-box" style="height:100%">
    <div class="content" :style="{ minHeight: curHeight+'px'}" style="padding-top: 1rem;">
      <!--导航-->
      <div class="hedeBox" style="position: fixed;left: 0;top: 0;width: 100%;z-index: 10;">
        <nav-head :title="title" ></nav-head>
      </div>
      <!--导航-->
      <!-- <div class="nav-box">
        <div class="nav-left">
          <img src="../../assets/image/market_details/back.png" alt="" style="width: 0.4rem;height: 0.42rem;">
        </div>
        <span class="nav-title">高级认证</span>
        <div class="nav-right"></div>
      </div> -->

      <div class="tipsPosi" v-if="authenticate == 4">
        <img src="../../assets/image/adopt.png" alt="">
        <div class="text">审核通过</div>
      </div>
      <div class="tipsPosi" v-if="authenticate == 3">
        <img src="../../assets/image/examine.png" alt="">
        <div class="text">审核中</div>
      </div>
      <div class="tipsPosi" v-if="authenticate == 6">
        <img src="../../assets/image/refuse.png" alt="">
        <div class="text">审核拒绝</div>
        <div class="userBut" @click="showBox">重新提交</div>
      </div>
      <div class="" v-if="authenticate == 2">
      <!--分割线-->
        <div class="dividing-line"></div>
        <!-- 认证模块 -->
        <div class="certificationBox">
          <!-- 正面 -->
          <div class="positive">
            <div class="imgBox">
              <img class="img" :src="positiveImg1" alt="">
              <img class="imgAdd" src="../../assets/image/add.png" alt="">
              <!-- 上传按钮 -->
              <div class="but">
                <van-uploader  :after-read="afterRead1" :max-count="1" />
              </div>
            </div>
            <div class="text">点击上传身份证正面</div>
          </div>
          <!-- 反面 -->
          <div class="positive">
            <div class="imgBox">
              <img class="img" :src="positiveImg2" alt="">
              <img class="imgAdd" src="../../assets/image/add.png" alt="">
              <!-- 上传按钮 -->
              <div class="but">
                <van-uploader  :after-read="afterRead2" :max-count="1" />
              </div>
            </div>
            <div class="text">点击上传身份证反面面</div>
          </div>
          <!-- 描述 -->
          <div class="tips">扫描标准：请勿上传缺少边角、模糊的身份证照片</div>
          <!--  -->
          <div class="positive">
            <div class="imgBox">
              <img class="img" :src="positiveImg3" alt="">
              <img class="imgAdd" src="../../assets/image/add.png" alt="">
              <!-- 上传按钮 -->
              <div class="but">
                <van-uploader  :after-read="afterRead3" :max-count="1" />
              </div>
            </div>
            <div class="text">点击上传手持身份证照片</div>
          </div>
        </div>
        <div class="butSub" @click="positiveSub()">上传</div>
      </div>
    </div>
  </div>
</template>

<script>
import { uploadImg, authenticat } from '@/api/user'
import Vue from 'vue'
import { Uploader, Toast } from 'vant'
import NavHead from '@/components/head.vue'
import Cryptojs from 'crypto-js'
import img1 from '../../assets/image/authenticationImg1.png'
import img2 from '../../assets/image/authenticationImg2.png'
import img3 from '../../assets/image/authenticationImg3.png'
Vue.use(Uploader).use(Toast)
export default {
  components: { NavHead },
  data () {
    return {
      url: this.GLOBAL.domainName,
      title: '高级认证', // 初级认证
      curHeight: '',
      authenticate: 0,
      //
      positiveImg1: img1,
      positiveImg2: img2,
      positiveImg3: img3,
      parameter: {
        u_id: 2,
        authenticate: 3,
        country: 0,
        name: 0,
        ctype: 0,
        cid: 0,
        front_photo: '/public/upload/2020-06-16/1592302710167044.png',
        reverse_photo: '/public/upload/2020-06-16/1592302716654067.png',
        handheld_photo: '/public/upload/2020-06-16/1592302713665274.png'
      }
    }
  },
  created () {
    this.parameter.u_id = sessionStorage.getItem('u_id')
    this.authenticate = this.$route.query.authenticate
    this.beforeMount()
  },
  methods: {
    beforeMount (height) {
      var h = document.documentElement.clientHeight || document.body.clientHeight
      this.curHeight = h
      console.log(this.curHeight, 'this.curHeight')
    },
    afterRead1 (file) {
      var that = this
      var fd = new FormData()
      fd.append('file111', file.file)
      uploadImg(fd).then(res => {
        console.log(res)
        if (res.data === '上传失败') {
          that.$toast({
            message: res.data,
            duration: 1500
          })
        } else {
          this.positiveImg1 = that.url + res.data
          this.parameter.front_photo = res.data
        }
      }).catch((err) => {
        console.log(err)
      })
    },
    afterRead2 (file) {
      var that = this
      var fd = new FormData()
      fd.append('file111', file.file)
      uploadImg(fd).then(res => {
        console.log(res)
        if (res.data === '上传失败') {
          that.$toast({
            message: res.data,
            duration: 1500
          })
        } else {
          // this.positiveImg2 = 'http://192.168.1.200' + res.data
          this.positiveImg2 = this.url + res.data
          this.parameter.reverse_photo = res.data
        }
      }).catch((err) => {
        console.log(err)
      })
    },
    afterRead3 (file) {
      var that = this
      var fd = new FormData()
      fd.append('file111', file.file)
      uploadImg(fd).then(res => {
        console.log(res)
        if (res.data === '上传失败') {
          that.$toast({
            message: res.data,
            duration: 1500
          })
        } else {
          this.positiveImg3 = this.url + res.data
          this.parameter.handheld_photo = res.data
        }
      }).catch((err) => {
        console.log(err)
      })
    },
    positiveSub () {
      var that = this
      //  front_photo: '/public/upload/2020-06-16/1592302710167044.png',
      //   reverse_photo: '/public/upload/2020-06-16/1592302716654067.png',
      //   handheld_photo: '/public/upload/2020-06-16/1592302713665274.png'
      if (that.parameter.front_photo === '/public/upload/2020-06-16/1592302710167044.png') {
        that.$toast({
          message: '身份证正面不能为空',
          duration: 1500
        })
        return
      }
      if (that.parameter.reverse_photo === '/public/upload/2020-06-16/1592302716654067.png') {
        that.$toast({
          message: '身份证反面不能为空',
          duration: 1500
        })
        return
      }
      if (that.parameter.handheld_photo === '/public/upload/2020-06-16/1592302713665274.png') {
        that.$toast({
          message: '手持身份证不能为空',
          duration: 1500
        })
        return
      }
      var sortToken = that.objKeySort(this.parameter)
      var token = that.sgin(sortToken)
      // console.log(token)
      this.parameter.token = token
      authenticat(this.parameter).then(res => {
        console.log(res)
        if (res.code === 0) {
          that.$toast({
            message: '提交成功',
            duration: 1500
          })
          that.authenticate = 3
          delete this.parameter.token
        }
      }).catch((err) => {
        that.$toast({
          message: err,
          duration: 1500
        })
        delete this.parameter.token
      })
    },
    /**
     * 排序A-Z
     */
    objKeySort (arys) {
      var newkey = Object.keys(arys).sort()
      var newObj = {} // 创建一个新的对象，用于存放排好序的键值对
      for (var i = 0; i < newkey.length; i++) {
        newObj[newkey[i]] = arys[newkey[i]]
      }
      return newObj // 返回排好序的新对象
    },
    sgin (arr) {
      console.log(arr)
      var str = ''
      var hash = ''
      var key = 'MDc2OWJkYWI0ZGJiMmMxMzBjNzA3MGQ5NTU0MDVkODE='
      // eslint-disable-next-line no-undef
      for (var index in arr) {
        // eslint-disable-next-line no-undef
        str += index + '=' + arr[index] + '&'
      }
      var strsgin = str.substring(0, str.length - 1)
      var newstr = strsgin + key
      console.log(newstr, 'newstr')
      hash = Cryptojs.HmacSHA256(newstr, key).toString()
      console.log(hash, '111')
      return hash
    }
  }
}
</script>
<style scoped>
  .content{
    background-color: #1B213B;
    color: white;
  }
  .film-box{
    position: relative;
  }
  .nav-box{
    height: 1.28rem;
    line-height: 1.28rem;
    background-color: #1B213B;
    display: flex;
    justify-content: space-between;
    padding: 0 0.3rem;
    padding-bottom: 0rem;
    color: white;
    font-size: 0.36rem;
  }
  .nav-box img{
    vertical-align: middle;
    flex: 1;
    margin-left: 0.2rem;
  }
  .dividing-line{
    width: 100%;
    height: 0.2rem;
    background-color: #000;
  }
  .certificationBox{
    background: #1B213B;
  }
  .certificationBox .positive{
    margin: 0.3rem;
    height: 3.8rem;
    position: relative;
    background: #161B31;
  }
  .certificationBox .positive .text {
    color: #DADADD;
    font-size: 0.28rem;
    position: absolute;
    left: 0rem;
    bottom: 0.4rem;
    width: 100%;
    text-align: center;
  }
  .certificationBox .positive .imgBox{
    width: 2.19rem;
    height: 1.5rem;
    position: absolute;
    left: 50%;
    top: 50%;
    margin-left: -1.095rem;
    margin-top: -0.85rem;
  }
  .certificationBox .positive .imgBox .img{
    width: 100%;
    height: 100%;
  }
  .certificationBox .positive .imgBox .imgAdd{
    width: 0.66rem;
    height: 0.66rem;
    position: absolute;
    left: 50%;
    top: 50%;
    margin-left: -0.33rem;
    margin-top: -0.33rem;
  }
  .certificationBox .positive .imgBox .but{
    position: absolute;
    left: 0rem;
    top: 0rem;
    width: 2.19rem;
    height: 1.5rem;
    z-index: 99;
  }
  .certificationBox .positive .imgBox .but >>> .van-uploader__upload{
    width: 2.19rem !important;
    height: 1.5rem !important;
    opacity: 0;
  }
  .tips{
    color:rgba(255,255,255,0.3);
    line-height:30px;
    font-size: 0.24rem;
    text-align: center;
  }
  .butSub{
    margin: 0 0.3rem 0.6rem 0.3rem;
    height: 0.8rem;
    line-height: 0.8rem;
    text-align: center;
    background:linear-gradient(-90deg,rgba(16,99,255,1),rgba(2,197,255,1));
    border-radius: 0.1rem;
    color: #ffffff;
  }
  .tipsPosi{
    width: 5rem;
    height: 5rem;
    position: fixed;
    left: 50%;
    top: 50%;
    margin-left: -2.5rem;
    margin-top: -2.5rem;
  }
  .tipsPosi .img{
    width: 3rem;
    height: 3rem;
    display: block;
    margin: 0 auto;
  }
  .tipsPosi .text{
    text-align: center;
    color: rgba(255, 255, 255, 0.7);
    font-size: 0.4rem;
    text-align: center;
    line-height: 0.4rem;
    margin-top: 0.25rem;
  }
  .userBut{
    border-radius: 0.5rem;
    margin: 0 auto;
    margin-top: 0.3rem;
    background: linear-gradient(-90deg,rgba(16,99,255,1),rgba(2,197,255,1));
    width: 2rem;
    padding: 0.1rem 0.1rem;
  }
</style>
